<template>
    <!-- center icon -->
    <div class="map-center-icon" data-enable="true">
        <div class="map-center-selected" data-enable="true" v-if="!panel">
            <div class="button" data-enable="true" @click="confirm">新增</div>
            <div class="line border-1px-l" data-enable="true"></div>
            <div class="button" data-enable="true" @click="cancel">取消</div>
        </div>
    </div>
    <!-- center icon -->
</template>

<script>
    export default {
        name: "mp-center-button",
        props: {
            panel: {
                type: Boolean,
                default: false
            }
        },
        methods: {
            confirm() {
                this.$emit('confirm')
            },
            cancel() {
                this.$emit('cancel')
            }
        }
    }
</script>

<style lang="stylus" scoped>
    .map-center-icon
        width 30px
        height 30px
        background url("../../../../static/icon/map_tagging.png") center center no-repeat
        background-size 100%
        .map-center-selected
            position absolute
            top -50px
            left -70px
            display flex
            align-items center
            height 40px
            width 170px
            background-color #fff
            border-radius 5px
            box-shadow 0 0 7px -2px rgba(100, 100, 100, .33)
            &:before
                content ''
                position absolute
                bottom 0
                left 50%
                width 0
                height 0
                border 5px solid #fff
                transform translate3d(-50%, 50%, 0) rotate(45deg)
            .button
                position relative
                flex 1
                height 40px
                line-height 40px
                font-size 14px
                text-align center
            .line
                width 1px
                height 60%

</style>



