<template>
    <div v-loading="loading" data-mu-loading-size="15">
        <mu-button style="padding: 0" @click="handlerClick"
                   :disabled="loading" small
                   icon>
            <mu-icon value="dvr" size="20"/>
            <mu-popover class="popover border-1px" :class="cs" placement="top" :open="open">
                <div class="map-address-button-content">
                    <div class="item border-1px-b" v-for="item in list" @click="onClickItem(item)">
                        <div style="margin-right: 10px">
                            <img class="icon" src="../../../../static/icon/detailed_collection.png">
                        </div>
                        <div class="title">{{item.name}}</div>
                    </div>
                </div>
            </mu-popover>
        </mu-button>
    </div>
</template>

<script>
    import Shelter from '../../../base/shelter'
    import {MapMixins} from "../../../common/js/mixins"
    import {userMixins} from "../../../common/mixins"
    import {queryIAddress} from "../../../api/personal"
    import {mapGetters} from 'vuex'


    export default {
        name: "map-address-button",
        mixins: [userMixins, MapMixins],
        data() {
            return {
                open: false,
                loading: false,
                list: [],
                params: {
                    pageNumber: 0,
                    pageSize: 10
                }
            }
        },
        props: {
            cs: {
                type: String,
                default: ''
            }
        },
        computed: {
            ...mapGetters({
                novice: 'novice/novice'
            })
        },
        methods: {
            // 点击按钮
            handlerClick() {
                if (this.novice === 1) return

                if (this.isLogin) {
                    this.loading = true
                    this.params.pageNumber = 0

                    queryIAddress(this.params).then(res => {
                        if (res.content.length) {
                            this.list = res.content
                            this.open = true
                        } else {
                            this.$toast('您还没有添加常用地址哦')
                        }

                        this.loading = false
                    })
                } else {
                    this.$toast('您还没有登录，不能查看常用地址哦')
                }
            },
            // 点击选项
            onClickItem(item) {
                this.setCenter(item.longitude, item.latitude)
                this.open = false
            }
        },
        watch: {
            open(value) {
                if (value) {
                    Shelter.show({
                        zIndex: '12000',
                        onClose: () => {
                            this.open = false
                        }
                    })
                } else {
                    Shelter.close()
                }
            }
        }
    }
</script>

<style lang="stylus" scoped>
    .popover
        position absolute
        bottom 100px
        right 20px
        box-shadow none
        border-radius 5px
        overflow hidden

    .map-address-button-content
        width 200px
        max-height 50vh
        overflow-x hidden
        overflow-y auto
        .item
            display flex
            align-items center
            padding 10px 15px
        .icon
            width 14px
            height 14px
        .title
            font-size 14px
</style>



