<template>
    <div class="views selection" data-enable="true">
        <navigate title="地图选点" position="absolute"/>

        <!-- 定位 -->
        <location-button class="map-button shadow map-location-button"/>
        <!-- 选择常用地址按钮 -->
        <address-button class="map-button shadow map-address-button" cs="selection-cs"/>

        <div class="place-wrapper border-1px-t shadow" v-loading="loading" data-mu-loading-size="30">
            <div class="scroll-wrapper" v-if="list.length">
                <mu-list class="place-list">
                    <mu-list-item class="place-list-item border-1px-b" v-for="(item, index) in list" :key="index">
                        <mu-list-item-action>
                            <mu-avatar :color="item.typeId | formatFind(icons, 'color', 'typeId')">
                                <img class="types-icon"
                                     v-lazy="{
                                             error: require('../../../static/image/error-place.png'),
                                             src: findTypes(item.typeId, icons, 'value', 'typeId')
                                         }">
                            </mu-avatar>
                        </mu-list-item-action>
                        <mu-list-item-content>
                            <mu-list-item-title>{{item.name}}</mu-list-item-title>
                            <mu-list-item-sub-title>
                                {{item.dist | formatDistance(item.dist > 1000 ? 100 : 1)}}
                                {{item.dist > 1000 ? '公里' : '米'}}
                            </mu-list-item-sub-title>
                        </mu-list-item-content>
                        <mu-list-item-action>
                            <mu-button color="primary" data-mu-loading-size="16"
                                       v-loading="item.loading"
                                       :disabled="item.loading"
                                       @click="selection(item, index)" flat>
                                确认
                            </mu-button>
                        </mu-list-item-action>
                    </mu-list-item>
                </mu-list>
            </div>
            <no-success text="附近没有推荐地点" height="100%" v-else/>
        </div>

        <center-button ref="center" panel/>
    </div>
</template>

<script>
    import Vue from 'vue'
    import Navigate from "../../base/navigate/navigate"
    import LocationButton from '../../components/map/map-location-button/map-location-button'
    import AddressButton from '../../components/map/map-address-button/map-address-button'
    import CenterButton from '../../components/map/map-center-button/map-center-button'
    import {appMixins, routeMixins} from "../../common/mixins"
    import {getNavigationPoint} from "../../api/navigation"
    import {saveOrUpdateIAddress} from "../../api/personal"
    import {screenToLocation} from "../../common/js/cordova/map"
    import {isArray, debounce} from "../../types/index"
    import NoSuccess from "../../components/no-success/no-success"

    const name = 'SELECTION'

    export default {
        name,
        mixins: [appMixins, routeMixins],
        data() {
            return {
                list: [],
                loading: false
            }
        },
        computed: {
            title() {
                switch (this.$route.name) {
                    case 'selection-sn':
                        return '选择起点'
                    case 'selection-en':
                        return '选择终点'
                    case 'selection-cl':
                        return '收藏地点'
                    default:
                        return '地图选点'
                }
            }
        },
        activated() {
            // 地图移动，刷新
            window.$types.map.selection.move = debounce(this.moveEnd, 300)

            // getMapLocation().then(res => {
            //     setMapCenterAt(res.selectionX, res.selectionY, true)
            // })

            window.Arcgis && window.Arcgis.getSelectCenter(res => {
                this.moveEnd()
            })
        },
        methods: {
            async moveEnd() {
                this.loading = true

                const {offsetTop, offsetLeft} = this.$refs.center.$el

                const point = await screenToLocation(offsetLeft, offsetTop)
                // const point = {y: 23.1265, x: 111.898}

                console.log('当前位置: ', point)

                this.list = isArray(await getNavigationPoint({
                    centerPoint: {
                        latitude: point.y,
                        longitude: point.x
                    },
                    distance: 3
                }))

                this.loading = false
            },
            selection(item) {
                if (this.$route.name === 'selection-sn') {
                    this.setRouteSn(item)
                    this.$router.go(-2)
                }
                if (this.$route.name === 'selection-en') {
                    this.setRouteEn(item)
                    this.$router.go(-2)
                }
                if (this.$route.name === 'selection-cl') {
                    item.loading = true

                    saveOrUpdateIAddress({
                        name: item.name,
                        longitude: item.longitude,
                        latitude: item.latitude
                    }).then(res => {
                        this.$toast('收藏成功')

                        this.$router.back()
                    }).catch(err => {
                        this.$toast('收藏失败')

                        item.loading = false
                    })
                }
            },
            findTypes() {
                return Vue.filter('formatFind')(...arguments)
            }
        },
        components: {
            NoSuccess,
            Navigate,
            CenterButton,
            LocationButton,
            AddressButton
        }
    }
</script>

<style lang="stylus">

    .selection
        width 100%
        height 100%
        > .map-location-button
            left 13px
            bottom 32%
            bottom calc(30% + 10px)
        > .map-address-button
            right 13px
            bottom 32%
            bottom calc(30% + 10px)
        > .place-wrapper
            position: absolute
            top 70%
            bottom 0
            left 0
            right 0
            z-index 100
            background-color #fff
            .scroll-wrapper
                width 100%
                height 100%
                .place-list
                    padding 0
                    .place-list-item
                        padding 10px 0
                    .types-icon
                        padding 8px
        > .map-center-icon
            position absolute
            top 35%
            top calc(35% + 46px)
            left 50%
            z-index 2001
            transform translate3d(-50%, -100%, 0)

    .selection-cs
        bottom 32% !important
        bottom calc(30% + 40px) !important
</style>



