<template>
    <mu-ripple v-loading="loading"
               data-mu-loading-overlay-color="#fff"
               data-mu-loading-size="15"
               data-mu-loading-color="#7e8c8d"
               data-mu-loading-class="load"
               @click="handlerLocation">
        <mu-icon value="gps_fixed" size="20"/>
    </mu-ripple>
</template>

<script>
    import {MapMixins} from "../../../common/js/mixins"
    import {mapGetters} from 'vuex'

    export default {
        name: "map-location-button",
        mixins: [MapMixins],
        data() {
            return {
                loading: false
            }
        },
        computed: {
            ...mapGetters({
                novice: 'novice/novice'
            })
        },
        methods: {
            // 点击定位
            handlerLocation() {
                if (this.novice === 0) return

                this.loading = true

                this.getLocation().then(res => {
                    this.setCenter(res.x, res.y, true).then(res => {
                        this.loading = false
                    }).catch(err => {
                        this.loading = false
                        this.$toast('定位失败')
                    })
                }).catch(error => {
                    this.loading = false
                    this.$toast(error)
                })
            }
        }
    }
</script>

<style lang="stylus" scoped>
</style>



